body, html {
  margin: 0;
  padding: 0;
  font-family: 'PT Sans Narrow', sans-serif;
  text-transform: uppercase;
}

.page-content {
  width: 100vw;
  display: flex;
  flex-direction: column;

  @media screen and (min-width: 768px){
    height: 100vh;
    flex-direction: row;
  }
}

.img-wrapper {
  flex: 2;
  display: flex;
  align-items: stretch;
  flex-direction: column;
  justify-content: center;
}

.img__lines {
  padding: 10px 0;
  border-top: solid 1px #F48873;
  border-bottom: solid 2px #F48873;
}

.img__lines--big {
  height: 20px;
  border-bottom: 5px solid #F48873;
}

img {
  width: 100%;
}

.info {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #F48873;
  position: relative;

  overflow: hidden;
  &__details {
  width: 80%;
}
}

h1 {
  margin: 0;
  font-size: 2em;
  color: #504A71;

  @media screen and (min-width: 768px){
  font-size: 4em;
  }
}

h2 {
  font-size: 1em;
  margin: 0;
  color: #FAFAFA;
  letter-spacing: 10px;
  @media screen and (min-width: 768px){
  font-size: 1.3em;
  }
}

.arrows {
  max-width: 75px;
  margin: 50px 0;
  filter: invert(100%);
}


.detail {
  padding: 10px;
  margin-bottom: 15px;
  text-align: center;
  background-color: #FAFAFA;
  color: #F48873;
  letter-spacing: 7px;

  &--map{
    cursor: pointer;
  }
  @media screen and (min-width: 768px){
    font-size: 1.3em;
  }
}

.map {
  width: 80%;
  position: absolute;
  top: 20px;
  left: 10%;
  opacity: 0;
  transform: translateX(100%);
  transition: all 300ms ease-out;

   &--visible {
    transform: translateX(0);
    opacity: 1;
   }
 }